<template>
  <div id="helloWorld">
    <yd-cell-group v-for="item,index in shopList" @click.native="pick(item)">
      <yd-cell-item>
        <span slot="left">商户</span>
        <span slot="right">{{item.name}}</span>
      </yd-cell-item>
      <yd-cell-item>
        <span slot="left">地址</span>
        <span slot="right">{{item.position}}</span>
      </yd-cell-item>
    </yd-cell-group>
  </div>
</template>

<script type="text/babel">


export default {

  data () {

    return {
         shopList:[]
    }
  },
  methods:{
    pick(e){
      window.location.href="http://hcb601.bjyfkj.net/index.php/index/weixin/wx_code?user_id="+e.id;
    },
    getData(){
      let param = new URLSearchParams()
      let that=this;
      this.$http.post('maroShopController.do?getAllShop', param)

        .then((res) => {
          console.log(res.data);

          if (res.data.success) {

            that.shopList=res.data.obj;
            console.log(that.shopList)
          } else {

          }
        })
    }
  },

  created(){
    this.getData();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



